/* eslint-disable no-debugger */

import h from "virtual-dom/h";
import diff from "virtual-dom/diff";
import patch from "virtual-dom/patch";
import Immutable from "immutable";
import helpers from "./helpers";

let state = Immutable.Map({
  items: Immutable.List(["nice", "jacket"])
});
let newState;
let time;
let root = helpers.qs("#ui");
let tree = Immutable.List([]);

function getNextString() {
  const words = "I'm saying the truth you jacket wearing nice maker doge".split(" ");
  return words[Math.floor(Math.random() * words.length)];
}

function render(data) {
  return h("div", {}, [
    h("span", { id: "count" }, data.get("items").size),
    h(
      "ul",
      {},
      data.get("items").map((item, i) =>
        h(
          "li",
          {
            id: i,
            onclick() {
              state.items.splice(i, 1);
              updateUI();
            }
          },
          item
        )
      )
    )
  ]);
}

function updateTimeUI() {
  helpers.qs("#time-pos").innerHTML = `Position ${time.get("pos") + 1} of ${time.get("history").size}`;
  const backBtn = helpers.qs("#back");
  const nextBtn = helpers.qs("#next");

  if (time.get("pos") <= 0) {
    backBtn.setAttribute("disabled", true);
  } else {
    backBtn.removeAttribute("disabled");
  }

  if (time.get("pos") >= time.get("history").size - 1) {
    nextBtn.setAttribute("disabled", true);
  } else {
    nextBtn.removeAttribute("disabled");
  }
}

function saveState() {
  time =
    time ||
    Immutable.Map({
      history: Immutable.List([]),
      pos: -1
    });
  time.get("history").concat(time.get("history").slice(time.get("pos") + 1));
  time.get("history").push(state);
  time.set("pos", time.get("pos") + 1);
  updateTimeUI();
}

function updateUI(loading) {
  if (!loading) {
    saveState();
  }

  const newTree = render(state);
  const patches = diff(tree, newTree);
  root = patch(root, patches);
  tree = newTree;
}

helpers.$on(helpers.qs("#add"), "click", () => {
  state.get("items").push(getNextString());
  updateUI();
});

helpers.$on(helpers.qs("#back"), "click", () => {
  time.set("pos", time.get("pos" - 1));
  updateTimeUI();
  state = time.get("history").get(time.get("pos"));
  updateUI(true);
});

helpers.$on(helpers.qs("#next"), "click", () => {
  time.set("pos", time.get("pos") + 1);
  updateTimeUI();
  state = time.get("history").get(time.get("pos"));
  updateUI(true);
});

updateUI();
